export default class Main {
  static renderMain() {
    const body = document.getElementById('body');
    const main = document.createElement('main');
    const ul = document.createElement('ul');
    const div = document.createElement('div');
    Main.includeTag(body, div, 'wrapper');
    Main.includeTag(div, main, 'main');
    Main.includeTag(main, ul, 'clips');
    ul.id = 'mainID';
    ul.classList.add('clip-novisibile');
  }

  static renderItem(author, date, countwath, description, nameOfClip, imgSrc, urlOfClip) {
    const ul = document.getElementById('mainID');
    const li = document.createElement('li');
    const divPreview = document.createElement('div');
    const h2 = document.createElement('h2');
    const a = document.createElement('a');
    const img = document.createElement('img');
    const divInfo = document.createElement('div');
    const pAuthor = document.createElement('p');
    const pDate = document.createElement('p');
    const pCountWatch = document.createElement('p');
    const pDescription = document.createElement('p');
    Main.includeTag(ul, li, 'clip');
    Main.includeTag(li, divPreview, 'clip-preview');
    Main.includeTag(divPreview, h2, 'clip-title');
    Main.includeTag(h2, a, 'clip-link');
    Main.includeTag(divPreview, img, 'clip-img');
    Main.includeTag(li, divInfo, 'clip-info');
    Main.includeTag(divInfo, pAuthor, 'clip-author');
    Main.includeTag(divInfo, pDate, 'clip-date');
    Main.includeTag(divInfo, pCountWatch, 'clip-countwatch');
    Main.includeTag(li, pDescription, 'clip-description');
    pAuthor.innerHTML = author;
    pDate.innerHTML = date.slice(0, -14);
    pCountWatch.innerHTML = countwath;
    pDescription.innerHTML = description;
    a.href = urlOfClip;
    a.innerHTML = nameOfClip;
    img.src = imgSrc;
    img.width = '320';
  }

  static includeTag(parent, child, className) {
    parent.appendChild(child);
    child.classList.add(className);
  }
}
